<template>
  <BaseLayout title="订单详情">

    <van-steps class="step" :active="status" v-show="status<4">
      <van-step>已下单</van-step>
      <van-step>已付款</van-step>
      <van-step>已发货</van-step>
      <van-step>已签收</van-step>
    </van-steps>

    <van-collapse class="bill" v-model="unfold">
      <van-collapse-item name="0" title="收货信息" :value="state.order.card == 0 ? '物流派送' : (state.order.card < 0 ? '电子卡密' : '提货卡')">
        
        <div v-if="state.order.card == 0">
          <van-cell>
            <template #title>{{state.order.consignee.name}}（{{state.order.consignee.mobile}}）</template>
            <template #label>
              {{state.order.consignee.province}} {{state.order.consignee.city}}  {{state.order.consignee.county}} {{state.order.consignee.address}}
            </template>
          </van-cell>
          <van-cell v-for="(v,k,index) in state.order.logistics" :key="k" :title="v.dt + (Object.keys(state.order.logistics).length > 1 ? '（包裹 ' + (index + 1) + '）' : '')" :label="v.msg" border is-link></van-cell>
        </div>

        <van-swipe class="swipe" v-else>
          
          <van-swipe-item class="sitem" v-for="(v,k) in state.cards" :key="k" :style="{background:v.bg}">
            <div v-if="state.order.card > 0">
              <div class="card">提货卡号：****{{v.token.substr(4)}}</div>
              <div class="tool">
                <van-icon name="more-o"  @click="onFinish() & copy(v.token)"> 复制卡号</van-icon>
                &nbsp;
                <van-icon name="photo-o" @click="onFinish() & save(v.token)"> 显示卡片</van-icon>
              </div>
            </div>

            <div v-if="state.order.card < 0">
              <div class="card">{{v.no}}</div>
              <div class="tool">
                <van-icon name="more-o" v-show="v.pwd && v.pwd.length > 0" @click="onFinish() & $copy(v.no + '\n' + v.pwd)"> 复制卡密</van-icon>
              </div>
              <div class="deadline" v-show="v.deadline && v.deadline.length > 0">
                有效期至：{{v.deadline}}
              </div>
            </div>
          </van-swipe-item>

          <template #indicator="{ active }">
            <div class="indicator">{{ active + 1 }} / {{state.cards.length}}</div>
          </template>
        </van-swipe>

      </van-collapse-item>

      <van-collapse-item name="1" title="商品信息" :value="'共 ' + (state.order.card <= 0 ? state.order.total : state.order.card + ' x ' + state.order.total / state.order.card) + ' 件'">
        <van-cell v-for="(v,k) in state.order.detail" :key="k" :label="v.spec" :style="{alignItems:'center'}" @click="onView(v)">
          <template #icon><van-icon class="img" :name="v.image" size="32"></van-icon></template>
          <template #title><div class="van-multi-ellipsis--l2">{{v.title}}</div></template>
          <template #extra>&nbsp;x{{v.num}}</template>
        </van-cell>
      </van-collapse-item>

      <van-collapse-item name="2" title="订单总额" :value="state.order.amount + ' 元'">
        <van-cell title="实付积分" :value="state.order.point + ' 分'"></van-cell>
        <van-cell title="实付现金" :value="state.order.cash + ' 元'"></van-cell>
      </van-collapse-item>

      <van-collapse-item name="3" title="交易状态" :value="state.order.result">
        <van-cell title="创建时间" :value="state.order.dt_create"></van-cell>
        <van-cell title="付款时间" :value="state.order.dt_pay"></van-cell>
        <van-cell title="发货时间" :value="state.order.dt_send"></van-cell>
        <van-cell title="完成时间" :value="state.order.dt_finish"></van-cell>
      </van-collapse-item>
    </van-collapse>

    <van-divider :style="{color:'#EEE'}"># {{id}} / {{state.order.extra}} #</van-divider>

    <van-action-bar>
      <van-action-bar-icon icon="chat-o" text="客服" />
      <van-action-bar-button v-if="state.order.status==0" type="warning" text="关闭订单" @click="onClose"/>
      <van-action-bar-button v-if="state.order.status==0" type="primary" text="立即付款" @click="onPay"/>
      <van-action-bar-button v-if="state.order.card==0 && state.order.status==2" type="success" text="确认收货" @click="onFinish"/>
      <van-action-bar-button v-if="state.order.card==0 && state.order.status>0" type="danger" text="申请售后" @click="onService"/>
    </van-action-bar>

    <van-popup v-model:show="state.popup"><van-image class="giftcard" :src="state.giftcard" width="300"><br/>长按图片保存到手机或分享给好友</van-image></van-popup>

  </BaseLayout>
</template>

<style scoped>
.step {
  padding: 10px;
}

.bill {
  margin: 10px 0;
  min-height: 200px;
}
.bill .img {
  margin-right: 10px;
  border-radius: 5px;
}

.swipe {
  width: 100%;
  height: 100px;
  overflow: hidden;
}
.indicator {
  position: absolute;
  left: 5px;
  top: 5px;
  padding: 2px 5px;
  color: #FFF;
  background: rgba(0, 0, 0, 0.5);
  font-size: 12px;
}
.card {
  margin-top: 30px;
  color: #666;
  font-size: 18px;
  font-weight: 800;
  text-align:center;
}
.tool {
  position: absolute;
  color: #333;
  font-size: 12px;
  right: 10px;
  bottom: 10px;
}
.deadline {
  position: absolute;
  color: #333;
  font-size: 12px;
  left: 10px;
  bottom: 10px;
}

.giftcard {
  margin: 10px;
  font-size: 12px;
  text-align: center;
}
</style>

<script>
import {reactive,ref} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  props: {
    id: {
      type: [String, Number],
      default: 0,
    },
  },

  setup() {
    const state = reactive({
      order: {},
      cards: [],

      popup: false,
      giftcard: "",
    });
    const status = ref(0);
    const unfold = ref(["0", "1"]);
    const active = ref(0);
    return {state, status, unfold, active};
  },

  created(){
    this.onLoad();
  },

  methods: {
    onLoad() {
      this.state.cards = [];
      this.$http.post("/mall/orderDetail?id=" + this.id).then((data) => {
        this.state.order = data;
        this.state.order.style = ["#CCC", "#666", "#00C", "#0C0", "#C00"][data.status];
        this.state.order.result = ["未付款", "已付款", "已发货", "交易成功", "交易关闭"][data.status];
        this.status = data.status;
        if (data.card != 0){
          for (var i in data.cards){
            data.cards[i].bg = "rgb(" + _.random(150,250) + "," + _.random(150,250) + "," + _.random(150,250) + ")";
          }
          this.state.cards = data.cards;
        }
        if (data.type == 1 && data.status > 0){
          this.$http.get("/jdmall/update?id=" + this.id);
        }
      });
    },

    onView(item) {
      let path = "/mall/item?id=" + item.spu;
      this.state.order.type == 1 && (path = "/jdmall/item?id=" + item.sku);
      this.$router.push(path);
    },

    onPay() {
      let url = "/mall/pay";
      this.state.order.type == 1 && (url = "/jdmall/pay");
      this.$http.post(url + "?id=" + this.id).then(this.onLoad);
    },

    onClose() {
      this.state.order.status == 0 && this.$http.post("/mall/orderClose?id=" + this.id).then(this.onLoad);
    },

    onFinish() {
      this.state.order.status == 2 && this.$http.post("/mall/orderFinish?id=" + this.id).then(this.onLoad);
    },

    onService() {
      this.$toast("内测中，稍候开放");
    },

    copy(token){
      let str = "提货卡号：" + token + "（请关注微信公众号“福鲤社”，点击下方菜单提货）";
      this.$copy(str);
    },
    save(token) {
      this.state.popup = true;
      this.state.giftcard = this.$host + '/mall/card?type=pick&token=' + token;
    },
  }
}
</script>
